/* /// SECTIONS */
.sim-page {
  height: 92vh;
  background-color: #e5e5e5;
  color: black;
  padding: 10px;
  min-height: 500px;
}

.sim-container-box {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 55%;
  margin-right: 10px;
}

.sim-results-box {
  height: 100%;
  width: calc(45% - 10px);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

/* /// LOADER */
#loader {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

/* /// SIM-NAV */

.title {
  background: linear-gradient(101.38deg, #006567 0%, #00aec7 100%);
  border-radius: 2px;
  display: flex;
  align-items: center;
  width: 100%;
  margin-bottom: 10px;
}

#sim-title {
  height: 48px;
  margin-bottom: 10px;
}

#sim-title h1,
#results-top-box h1,
#results-emissions h1,
#results-button h1 {
  font-family: "Ruda", sans-serif;
  font-size: 20px;
  font-weight: normal;
  color: #ffffff;
  padding-bottom: 0;
  margin-bottom: 0;
  line-height: 20px;
  margin-left: 16px;
}

#sim-nav-box {
  align-items: center;
  height: 120px;
  padding: 10px;
  background-color: white;
  width: 100%;
  margin-bottom: 10px;
}

#sim-nav-box h2 {
  /* font-family: "Circular Std"; */
  text-transform: uppercase;
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 18px;
}

#sim-nav-box > div {
  width: 100%;
  justify-content: space-between;
  margin-top: 0.3em;
}

#sim-nav-fr {
  width: 72%;
  justify-content: space-between;
}

#sim-nav-world {
  width: 13%;
}

.sim-nav {
  width: 100%;
}

.sim-nav-categories {
  width: 100%;
  border-top: solid rgba(0, 0, 0, 0.2) 1px;
  justify-content: space-between;
  padding-top: 5px;
}

.sim-nav-category {
  justify-content: space-between;
  align-items: center;
  border-radius: 5px;
  position: relative;
  display: inline-block;
  height: 45px;
}

.sim-nav-category .sim-nav-category-background {
  position: absolute;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  border-radius: 5px;
  opacity: 0;
  transition: 1s;
}

.sim-nav-category:hover .sim-nav-category-background {
  opacity: 1;
}

.sim-nav-category .sim-nav-category-icon {
  position: absolute;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  text-align: center;
  display: inline-block;
  vertical-align: middle;
  width: 100%;
}

.sim-nav-category .sim-nav-category-icon-helper {
  display: inline-block;
  height: 100%;
  vertical-align: middle;
}

.sim-nav-category img {
  width: 90%;
  max-width: 30px;
  display: inline-block;
  vertical-align: middle;
}

#sim-nav-fr .sim-nav-category {
  width: 16%;
}

#sim-nav-world .sim-nav-category {
  width: 95%;
}

#options {
  width: 12%;
  margin-top: 31px;
  border: none;
}

/* /// MAIN-BOX */
.sim-main-box {
  background-color: white;
  height: auto;
  scroll-behavior: smooth;
  overflow-y: auto;
  padding: 10px;
  height: calc(100% - 188px);
  width: 100%;
}

/* /// TITRES CATEGORIES */

.sim-categorie {
  padding: 1.5% 1em;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2%;
  height: auto;
  width: 100%;
  text-align: center;
}

.sim-categorie > div {
  width: 55%;
  justify-content: space-between;
  align-items: center;
}

.sim-jauge {
  width: 100%;
  height: 20px;
  margin: 5px 0;
}

.sim-categorie-emissions {
  font-size: 15px;
  line-height: 19px;
}

.sim-categorie-markers {
  /* justify-content: space-evenly; */
  align-items: baseline;
  width: 100%;
}

.sim-categorie-markers > div {
  align-items: center;
  width: 24%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.MuiTooltip-tooltipArrow {
  background-color: blue;
}

.sim-categorie-markers > div > div {
  width: 100%;
}

/* 
.sim-categorie-markers > div > div:last-of-type {
  width: 70%;
} */

.sim-categorie-markers p {
  font-size: 11px;
  text-align: center;
}

.sim-emissions-2030 {
  color: #0b8c85;
}

.sim-emissions-2020 {
  color: #ff6868;
}

.sim-emissions-2020 span {
  font-size: 9px;
}

.sim-emissions-2030 span {
  font-size: 9px;
}

.sim-categorie-name {
  font-weight: bold;
  font-size: 23px;
  line-height: 32px;
  text-align: left;
  width: 45%;
}

/* /// OPTIONS */

#optionsContainer {
  position: fixed;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  text-align: center;
  z-index: 9999;
}

#optionsContainer .optionsContainerBackground {
  position: absolute;
  top: 0px;
  left: 0px;
  bottom: 0px;
  right: 0px;
  background: rgba(0, 0, 0, 0.5);
}

#optionsContainer .optionsContainerClose {
  position: absolute;
  top: 10px;
  right: 20px;
  font-size: 2rem;
  cursor: pointer;
}

#scrollOptions {
  align-items: center;
  padding: 20px;
  border-radius: 10px;
  color: #163e59;
}

#scrollOptions > div {
  justify-content: center;
}

.sim-options {
  justify-content: space-between;
  align-items: center;
  padding: 1em 1em 0;
}

.sim-option-box {
  width: 90%;
  margin-bottom: 30px;
}

.sim-option-box p {
  margin-bottom: 0.5em;
  font-size: 0.8em;
}

.sim-option-box label {
  font-size: 1em;
  margin-right: 0.5em;
}

.sim-option-form {
  justify-content: flex-start;
}

.sticky {
  margin-right: -0.3em;
  z-index: 2;
  position: sticky;
  position: -webkit-sticky;
  padding-bottom: 20px;
  margin-bottom: 10px;
  width: 100%;
  max-width: 800px;
  margin: auto;
  text-align: left;
  margin-top: 150px;
}

.sticky .sim-categorie {
  background: #b0e0e6;
}

.sim-param-box {
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-column-gap: 20px;
  grid-row-gap: 20px;
  margin-bottom: 2%;
}

.sim-param-radio {
  margin-right: 1em;
}

input[type="radio"] {
  margin-right: 1em;
}

.bigger {
  height: 2.2em;
}

/* /// JAUGES */
.jauge-ext {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.tooltip {
  /* font-weight: 900 !important;
  font-size: 15px !important;
  line-height: 18px !important; */
  position: absolute;
  left: 50%;

  text-align: left;
  white-space: nowrap;
  -webkit-transform: translate(-50%, -150%);
          transform: translate(-50%, -150%);
  transition: all 0.15s ease-in-out;
}

.tooltip::before {
  content: "";
  position: absolute;
  top: 100%;
  left: 50%;
  width: 0;
  height: 0;
  border: 0.5em solid transparent;
  border-top-color: #000;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
}

.marker1,
.marker2 {
  z-index: 3;
}
