@media screen and (max-width: 1110px) {
  body {
    font-size: 3px;
  }

  .sim-container-box {
    width: 50%;
  }

  .sim-results-box {
    width: calc(50% - 10px);
  }
}

@media screen and (min-height: 1100px) {
  #results-emissions-charts-container {
    grid: 40% 6% 40% 6% / 60%;
    grid-gap: 5%;
    gap: 5%;
    justify-content: center;
    grid-template-areas:
      "g1"
      "g2"
      "g3"
      "g4";
  }
}

@media screen and (max-height: 700px) {
  .sim-categorie-emissions {
    font-size: 11px;
  }

  #results-top-box {
    height: 150px;
  }

  #results-bottom-box {
    height: calc(100% - 150px);
  }

  .results-title {
    font-size: 0.8em;
  }

  .results-figure {
    font-size: 1.2em;
    width: 5em;
    height: 2.5rem;
    line-height: 2.5rem;
  }

  .b5 {
    line-height: normal;
  }

  #results-impacts-box2 .results-figure {
    font-size: 1.2rem;
  }

  .results-legend {
    font-size: 0.6em;
  }

  .b9 {
    font-size: 0.5em;
  }

  #results-emissions-charts-container {
    height: 45%;
    grid-gap: 2px 15px;
  }

  #results-emissions-charts-container p {
    font-size: 1em;
  }

  .sim-results-box button {
    padding: 0.4em 2em;
    font-size: 1.2em;
  }
}

