/* /// FONT */



.bold-text {
    font-weight: 600;
}

.light-text {
    font-weight: 300;
}

/* /// DEBUG */
* {
    /* border: 1px solid red; */
    margin-bottom: 0;
}

#mobile-message {
    visibility: hidden;
}

/* /// COLORS */
:root {
    /* /// BACKGROUND */
    --backVSGP: linear-gradient(101.38deg, #006567 0%, #00aec7 100%);
    --backgreen: linear-gradient(135deg, rgb(0, 124, 161), rgb(126, 212, 164));
    --backwhite: rgb(250, 255, 250, 0.8);

    /* /// CHART */
    --darkblue: #012340;
    --darkpurple: #312559;
    --purple: #8B4F8C;
    --green: #77D9B5;
    --lightgreen: #88F2B6;
    --grey: rgb(228, 228, 228);
    --darkgrey: darkgrey;
    --lightgrey: #E5EAEC;

    /* /// TEMP DATA */
    --tempgreen: rgb(56, 177, 126);
    --tempyellowgreen: rgb(163, 207, 91);
    --tempyellow: rgb(255, 230, 0);
    --tempyelloworange: rgb(255, 153, 0);
    --temporangered: rgb(255, 94, 0);
    --tempred: crimson;
    --tempredblack: rgb(119, 2, 2);
}

/* /// APP SETUP */
#root {
    width: 100vw;
    height: 100vh;
    margin: 0;
    padding: 0;
    min-width: 1024px;
    max-width: 1400px;
}

html {
    scroll-behavior: smooth;
}

body {
    background-image: linear-gradient(101.38deg, #006567 0%, #00aec7 100%);
    background-image: var(--backVSGP);
    background-attachment: fixed;
    color: white;
    font-size: 1rem;
    display: flex;
    justify-content: center;
    font-family: 'Ruda', sans-serif;;
}

#content-main {
    width: 100%;
    height: 100vh;
    margin: 0;
}

.light {
    color: "black";
}

.border {
    border-bottom: 2px solid #77D9B5;
    border-bottom: 2px solid var(--green);
    border-image-slice: 1;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

/* /// GLOBAL */
.hidden {
    visibility: hidden;
}

.displaynone {
    display: none;
}

.flex-item {
    display: flex;
}

.flex-column {
    flex-direction: column;
}

.grid-item {
    display: grid;
}

h1 {
    font-size: 5em;
    margin-bottom: 0.2em;
    line-height: 5rem;
}

h2 {
    font-size: 3em;
    margin-bottom: 0.2em;
    line-height: 3rem;
}

h3 {
    font-size: 2em;
    margin-bottom: 0.2em;
    line-height: 2rem;
}

h4 {
    font-family: 'Rajdhani', sans-serif;
    text-transform: uppercase;
    font-size: 24px;
    font-weight: bold;
    color:black;
    margin-bottom: 2px;
    line-height: 1.5rem;
}

.nopad {
    padding: 0;
}

.nomarge {
    margin: 0;
}

.margeup {
    padding-top: 1rem !important;
}

#loader h4 {color: white}

